.Doge {
    width: 10vw;
}

button.dogeButt {
    width: 6.2em;
    height: 5em;
    font-style: bold;
    font-size: 1em;
    align-content: center;
    display: inline-block;
    margin-bottom: 1rem;
    border-radius: 5%;
}

button.dogeButt.forever {
    background-image: url("https://i.imgur.com/wvByHGD.jpg");
    background-size: cover;
    color: #11111000
}

button.dogeButt:hover {
    background-image: url("https://i.imgur.com/wvByHGD.jpg");
    background-size: cover;
    color: #11111000
}
button.dogeButt:active {
    background-image: url("https://i.imgur.com/1YLZx38.jpg");
    background-size: cover;
    color: #11111000
}

button.inactive {
    display: none;
}

button.active {
    width: 6em;
    height: 3em;
    font-style: bold;
    font-size: 0.8em;
    align-content: center;
    display: block;
    margin-bottom: 1rem;
    border-radius: 5%;
    margin: 0 auto;
}

hr.s {
    display: block;
    width: 30vw;
}

hr.m {
    display: block;
    width: 50vw;}

hr.l {
    display: block;
    width: 80vw;
}